import React from "react";

//creating react component
export default function ProjetDetail(props) {
    let emojis = ["🔥", "👀", "⭐", "💻", "🐧","🐭","👋","🍕","🎷","🙌"];
  return (
    <div class="projectTile">
      <div class="projectHeader">
        <h1>{props.content.titre}</h1>
        <ul style={{paddingLeft:"16px"}}>
          {props.content.skilz.map((skill) => {
            return <li>{skill}</li>;
          })}
        </ul>
      </div>
      <div className="projectDescription">
          {props.content.description.split("//").map((line) => {
              return(<p>{line}</p>);
          })}
          {props.content.link != "" ? <a class="projLink" href={props.content.link} target="_blank" rel="noopener noreferrer">{emojis[Math.floor(Math.random()*emojis.length)]} Voir le projet</a> : <div></div>}
      </div>
    </div>
  );
}
