import React from "react";
import "/style/slider.css"
//creating react component
export default function Projets(props) {
  function handleClick(e) {
    console.log()
    props.stateSet(props.content[e.target.value]);
  }
  return (
    <div class="projsZone">
      <input type="range" min="0" max={props.content.length -1} onChange={handleClick} />
      <div class="ticks">
      {props.content.map((projet, index) => {
        return (
          <span id={index} class="tick">
            {projet.yr}
          </span>
        );
      })}
      </div>
    </div>
  );
}
